/**
 *  YYYY-MM-DD 
 * @param {Date} date 
 * @returns {string}
 */
export const formatDate = (date) => {
  if (!date) return '';
  if (!(date instanceof Date)) {
    date = new Date(date);
  }
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0');
  const day = String(date.getDate()).padStart(2, '0');
  return `${year}-${month}-${day}`;
};

/**
 *  Date 
 * @param {string} dateString 
 * @returns {Date}
 */
export const parseDate = (dateString) => {
  if (!dateString) return null;
  if (dateString instanceof Date) return dateString;
  return new Date(dateString);
};

/**
 * 
 * @param {Date|string} startDate 
 * @param {Date|string} endDate 
 * @returns {number}
 */
export const daysBetween = (startDate, endDate) => {
  const start = parseDate(startDate);
  const end = parseDate(endDate);
  
  // 
  start.setHours(0, 0, 0, 0);
  end.setHours(0, 0, 0, 0);
  
  // 
  const diffTime = Math.abs(end - start);
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  
  return diffDays;
};

/**
 * 
 * @param {Date|string} date 
 * @param {number} days 
 * @returns {Date}
 */
export const addDays = (date, days) => {
  const result = new Date(parseDate(date));
  result.setDate(result.getDate() + days);
  return result;
};

/**
 * 
 * @param {Date|string} date 
 * @returns {number}
 */
export const getWeekNumber = (date) => {
  const d = parseDate(date);
  d.setHours(0, 0, 0, 0);
  d.setDate(d.getDate() + 4 - (d.getDay() || 7));
  const yearStart = new Date(d.getFullYear(), 0, 1);
  const weekNumber = Math.ceil((((d - yearStart) / 86400000) + 1) / 7);
  return weekNumber;
};

/**
 * 
 * @returns {string}
 */
export const randomColor = () => {
  const letters = '0123456789ABCDEF';
  let color = '#';
  for (let i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
};

/**
 * 
 * @param {Array} tasks 
 * @returns {Object} { startDate, endDate }
 */
export const calculateAutoDateRange = (tasks) => {
  if (!tasks.length) {
    const today = new Date();
    const oneMonthLater = new Date();
    oneMonthLater.setMonth(today.getMonth() + 1);
    return { startDate: today, endDate: oneMonthLater };
  }

  let minDate = parseDate(tasks[0].start);
  let maxDate = parseDate(tasks[0].end);

  tasks.forEach(task => {
    const taskStart = parseDate(task.start);
    const taskEnd = parseDate(task.end);

    if (taskStart < minDate) minDate = taskStart;
    if (taskEnd > maxDate) maxDate = taskEnd;
  });

  // 7
  minDate = addDays(minDate, -7);
  maxDate = addDays(maxDate, 7);

  return { startDate: minDate, endDate: maxDate };
};

// 
const utilsExport = {
  formatDate,
  parseDate,
  daysBetween,
  addDays,
  getWeekNumber,
  randomColor,
  calculateAutoDateRange
};

// 
export default utilsExport; 